$(".about-nav__current").html($(".about-nav__link._active").html())


$(".mob-tab__show").click(function () {
    $(this).parents(".mob-tab").toggleClass("_open")
    $(this).siblings(".mob-tab__hidden").slideToggle()
})

$(".about-nav__current").click(function () {
    $(".about-nav").toggleClass("_open")
})

$(".equipment2-card__more").click(function () {
    $(this).parents(".equipment2-card").toggleClass("_show-all")
})

$(document).mouseup( function(e){
    let div = $( ".about-nav,.header-search,.header" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});
