function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")

        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function SlidersLine() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider-line")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)

        SlidersArray[index] = new Swiper(`.slider-line[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            speed: 5000,
            loop: true,
            allowTouchMove: false,
            autoplay: {
                delay: 0,
                disableOnInteraction: false // или сделать так, чтобы восстанавливался autoplay после взаимодействия
            },
        })
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            spaceBetween: 0,
            slidesPerView: "auto",
            freeMode: true,
            watchSlidesProgress: true,
            direction: "vertical",
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 0,
            direction: "vertical",
            loop: true,
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
            thumbs: {
                swiper:  gallery.mySwiper,
            },
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });

        SlidersArray[index] = gallery
    })

}



function storiesSliders() {
    const sliderDelay = 15000

    let SlidersArray = []

    let sliders = document.querySelectorAll(".story-slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")

        SlidersArray[index] = new Swiper(`.story-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: true,
            speed: 500,
            autoplay: {
                delay: sliderDelay,
                disableOnInteraction: false,
            },
            navigation: {
                nextEl: `.story-slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.story-slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
        })

        SlidersArray[index].on('slideChangeTransitionEnd', function() {
            console.log(true)
            setTimer(sliderDelay,slider)
        });

        if (slider) {
            setTimer(sliderDelay,slider)
        }

        $(".story-card").click(function () {
            SlidersArray[index].slideTo(0,0)
            $(".story-modal").addClass("_active")
            $("body").addClass("fixed-body")
        })

        $("._close-story").click(function () {
            $(".story-modal").removeClass("_active")
            $("body").removeClass("fixed-body")
        })
    })
}

function setTimer(delay,slider) {
    let activePagination = slider.querySelector(".swiper-slide-active .story-slider__progress span")
    activePagination.style.animationDuration = `${delay}ms`
}


Sliders()
gallery()
SlidersLine()
storiesSliders()
